import React , {Component} from 'react';
import { Redirect } from 'react-router-dom';
import '../css/css.css';
import $ from 'jquery';

import {setLogin} from "../index";

let style = {
    backgroundColor: '#8dc63f',
    fontSize: 20,
    fontWeight: 500,
    height: 52,
    padding: '0 3vmin',
    borderRadius: 5,
    color: '#fff'
};

class UserInfo extends Component {

    constructor() {
        super();
        this.setPassword = this.setPassword.bind(this);
    }

    OldUserInfo = {username: "", password: "", email: "", phonenumber: ""};
    NewUserInfo = {username: "", password: "", email: "", phonenumber: ""};

    setPassword(e) {
        this.NewUserInfo.password = e.target.value;
    }
    setEmail(e) {
        this.NewUserInfo.email = e.target.value;
    }
    setPhonenumber(e) {
        this.NewUserInfo.phonenumber = e.target.value;
    }

    handleModifyClick = () => {
        let new_password = this.state.password;
        let new_email = this.state.email;
        let new_phonenumber = this.state.phonenumber;
        let re1 =  /^[0-9a-zA-Z]*$/g;
        let err = "";
        if(!re1.test(new_password)) {
            err += "password must contain both letters and numbers\n";
        }
        let re2 = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
        if(!re2.test(new_email)) {
            err += "invalid email format\n";
        }
        let re3=/^(13+\d{9})|(159+\d{8})|(153+\d{8})$/;
        if(!re3.test(new_phonenumber)) {
            err += "invalid phonenumber";
        }
        if(err !== "") {
            alert(err);
        }
        else {
            $.ajax({
                url:"/cuser",
                data:{
                    username:this.state.username,
                    password:new_password,
                    email:new_email,
                    phonenumber:new_phonenumber
                },
                context:document.body,
                async:false,
                type:"get"
            });
            alert("Modify success");
        }
    };

    render() {
        let user = null;
        $.ajax({
            url:"/quser",
            data:{
                id:this.props.UserID
            },
            context:document.body,
            async:false,
            type:"get",
            success:function(data) {
                if(data !== "null") {
                    user = $.parseJSON(data);
                }
            }
        });
        console.log(user);
        this.NewUserInfo.username = user.username;
        this.OldUserInfo.username = user.username;
        this.OldUserInfo.password = user.password;
        this.OldUserInfo.email = user.email;
        this.OldUserInfo.phonenumber = user.phonenumber;

        return (
            <div className="Inf">
                <h4>
                    <p className="Header">Username: </p>
                    <p className="Content">{this.OldUserInfo.username}</p>
                </h4>
                <h4>
                    <p className="Header">PassWord: </p>
                    <input type="text" value={this.NewUserInfo.password} onChange={this.setPassword}/>
                </h4>
                <h4>
                    <p className="Header">Email: </p>
                    <input type="text" value={this.NewUserInfo.email} onChange={this.setEmail}/>
                </h4>
                <h4>
                    <p className="Header">PhoneNumber: </p>
                    <input type="text" value={this.NewUserInfo.phonenumber} onChange={this.setPhonenumber}/>
                </h4>
                <button style={style} onClick={this.handleModifyClick}>Modify</button>
            </div>
        );
    }
}

class Profile extends Component {
    constructor() {
        super();
        this.state = {redirect: false};
    }

    handleCancelClick = () => {
        this.setState({redirect: true});
    };

    handleLogoutClick = () => {
        const confirmed = window.confirm('Sure to Logout?');
        if(confirmed) {
            $.ajax({
                url: "/userlogout",
                context: document.body,
                async: false,
                type: "get"
            });
            setLogin(false, null);
            this.setState({redirect: true});
        }
    };

    render() {
        // let uid = "";
        // let islogin = false;
        // $.ajax({
        //     url:"/checkstate",
        //     context:document.body,
        //     async:false,
        //     type:"get",
        //     success: function(data) {
        //         if(data !== "null") {
        //             uid = data;
        //             islogin = true;
        //         }
        //     }
        // });
        // if(islogin) {
        //     setLogin(true, uid);
        // }
        // else {
        //     setLogin(false, null);
        // }
        if (this.state.redirect) {
            return (
                <Redirect push to="/booklist"/>
            );
        }
        return (
            <div>
                <UserInfo UserID={this.props.location.state.userid}/>
                <div className="Button">
                    <button style={style} onClick={this.handleCancelClick}>Back</button>
                    <button style={style} onClick={this.handleLogoutClick}>Logout</button>
                </div>
            </div>
        );
    }
}

export default Profile;


